<template>
  <div class="about-container" v-loading="loading || !setLoaded">
    <iframe v-if="src" class="about-content" frameborder="0" :src="src" @load="setLoaded = true"></iframe>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'About',
  data () {
    return {
      setLoaded: false
    }
  },
  computed: mapState('about', {
    src: 'data',
    loading: 'loading'
  }),
  created () {
    this.$store.dispatch('about/fetchAbout')
  }
}
</script>

<style scoped lang="less">
.about-container{
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
}
.about-content{
  width: 100%;
  height: 100%;
  display: block;
  box-sizing: border-box;
}
</style>
